/**
 * Help Tooltip strings.
 */
const help = {};

help.title = (
	`Title of the work youre attributing. You can use something general like this work or this image if you cant find a specific title.

	URL is the link to the original source. If you cant find the URL, say where you got the work, e.g. Brooklyn Bridge photograph from Wikimedia Commons.`
);

help.authorName = (
	`Name of the author(s) of the material you are attributing. If you cant find the name, you can list the authors screen or username. If available, enter the URL to the authors website or user account page.

	If an organization is listed but no individual author you can (1) use the organization name as the author, or (2) leave the author field blank and fill in the organization field below.`
);

help.publisher = (
	`If available, add the name of the organization that sponsored or managed the work you are adopting; its always nice to acknowledge those entities for their contribution.`
);

help.project = (
	`If available, add the name of the project associated with the work. For example, Open Learning Initiative is the name of a project managed by Carnegie Mellon University.

	If available, add the project URL.`
);

help.datePublished = (
	`Include the date that the work youre attributing was published. Leave this blank if you cant find the date or are unsure.`
);

help.license = (
	`Choose the exact Creative Commons license given to the work youre attributing.

	If the work is in the public domain, you can choose whether its in the general public domain (General) or if its officially released under the Creative Commons public domain (CC0).`
);

help.derivative = (
	`If you are attributing a work derived from other works, please add a link to the original.`
);

export default help;
